import React from 'react'

import Table from '@mui/material/Table';
import TableBody from '@mui/material/TableBody';
import TableCell from '@mui/material/TableCell';
import TableContainer from '@mui/material/TableContainer';
import TableHead from '@mui/material/TableHead';
import TableRow from '@mui/material/TableRow';
import { Typography } from '@mui/material';
import Grid from '@mui/material/Grid';
import Box from '@mui/material/Box';

// function createData(noOfPacks, noOfPallets, qty, partial){
//     return { noOfPacks, noOfPallets, qty, partial }
// }



const DisplayData = ({ data, style }) => {
    const {packSize,noOfPackKG,labelType, rows} = JSON.parse(data);
    // JSON.parse(data);

    // const rows = [
    //     createData(noOfPacks,noOfPallets,qty,partial)
    // ]

  return (
    <React.Fragment>
    <Box style={style}>
      <Grid container marginBottom={2}>
        <Grid item xs={6}>
          <Typography variant='body2' component="span">Pack Size : </Typography>
          <Typography variant='body2' component="span" fontWeight={600}>{packSize} ST</Typography>
        </Grid>
        <Grid item xs={6}>
          <Typography variant='body2' component="span">No Pack Size : </Typography>
          <Typography variant='body2' component="span" fontWeight={600}>{noOfPackKG} KG</Typography>
        </Grid>
        <Grid item xs={6}>
          <Typography variant='body2' component="span">Label Type : </Typography>
          <Typography variant='body2' component="span" fontWeight={600}>{labelType}</Typography>
        </Grid>
      </Grid>

      <TableContainer>
        <Table size='small' sx={{border: '1px solid gray'}}>
          <TableHead>
            <TableRow>
              <TableCell align='center' sx={{fontWeight:"600"}}>No. of packs</TableCell>
              <TableCell  align='center' sx={{fontWeight:"600"}}>No. of pallets</TableCell>
              <TableCell  align='center' sx={{fontWeight:"600"}}>Qty</TableCell>
              <TableCell  align='center' sx={{fontWeight:"600"}}>Partial</TableCell>
            </TableRow>
          </TableHead>
          <TableBody>
            {rows.map((row, i) => (
              <TableRow key={i}>
                <TableCell  align='center'>{row.noOfPacks}</TableCell>
                <TableCell  align='center'>{row.noOfPallets}</TableCell>
                <TableCell  align='center'>{row.qty}</TableCell>
                <TableCell  align='center'>{row.partial}</TableCell>
                {/* <TableCell  align='center'>{String(row.partial) === 'true' ? "Yes" : "No"}</TableCell> */}
              </TableRow>
            ))}
          </TableBody>
        </Table>
      </TableContainer>
      </Box>
    </React.Fragment>
  );
};

export default DisplayData;
