import React, {useState} from "react";

import Grid from '@mui/material/Grid';
import TextField from '@mui/material/TextField';
import Checkbox from '@mui/material/Checkbox';

function FormRow({rowId, setRows }) {
    // const [noOfPacks, setNoOfPacks] = useState('');
    // const [noOfPallets, setNoOfPallets] = useState('');
    // const [qty, setQty] = useState('');
    // const [partial, setPartial] = useState();

    const [rowData, setRowData] = useState({
        noOfPacks: '',
        noOfPallets: '',
        qty: '',
        partial: ''
    })
    
    const handleInput = (e, rowNo) => {
        const {name, value} = e.target;
        // setRowData({...rowData, [name] : value})  
        setRowData({...rowData, [name]: value})
        setRows((prev) => {
         return {...prev, [rowId] : {...rowData,[name]: value}}
        })
    }

    const handleChange = (e) => {
        const {name, checked} = e.target;
        // console.log(checked)
        setRowData({...rowData, [name] : checked}) 
        setRows((prev) => {
          return {...prev, [rowId] : {...rowData,[name]: checked ? 'Yes' : 'No'}}
        })    
    }

    return (
      <React.Fragment>
        <Grid container spacing={1} id={rowId}>
          <Grid item xs={4}>
            {/* <TextField variant='outlined' onChange={(e) => handleInput(e,rowId)} name="noOfPacks" value={rows[rowId].rowData.noOfPacks}  size='small' placeholder='Ex: 25'/> */}
            <TextField variant='outlined' onChange={(e) => handleInput(e, rowId)} name="noOfPacks" value={rowData.noOfPacks}  size='small' placeholder='Ex: 25'/>
          </Grid>
          <Grid item xs={4}>
            {/* <TextField variant='outlined' onChange={(e) => handleInput(e,rowId)} name="noOfPallets" value={rows[rowId].rowData.noOfPallets} size='small' placeholder='Ex: 5'/> */}
            <TextField variant='outlined' onChange={(e) => handleInput(e, rowId)} name="noOfPallets" value={rowData.noOfPallets} size='small' placeholder='Ex: 5'/>
          </Grid>
          <Grid item xs={2}>
            {/* <TextField variant='outlined' onChange={(e) => handleInput(e,rowId)} name="qty" value={rows[rowId].rowData.qty} size='small' placeholder='Ex: 125'/> */}
            <TextField variant='outlined' onChange={(e) => handleInput(e, rowId)} name="qty" value={rowData.qty} size='small' placeholder='Ex: 125'/>
          </Grid>
          <Grid item xs={2}>
            {/* <Checkbox sx={{ '& .MuiSvgIcon-root': { fontSize: 28 }}} onChange={handleChange} name="partial" size='small' /> */}
            <Checkbox sx={{ '& .MuiSvgIcon-root': { fontSize: 28 } }} onChange={(e) => handleChange(e, rowId)} name="partial" size='small' />
          </Grid>
        </Grid>
      </React.Fragment>
    );
  }

  export default FormRow;